
.movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.7),0 8px 16px -8px rgba(0,0,0,0.7);
}

.movie img{
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    position: relative;
    top: -50px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.5),0 8px 16px -8px rgba(0,0,0,0.5);
}

.movie .movie__title{
    font-size: 24px;
    margin: 0px;
}

.movie .movie__year{
    margin: 0px;
    color: grey;
}

.movie .movie__genres{
    color: grey;
    display: inline-flex;
    list-style: none;
    padding: 0;
    margin: 0;
}

.movie .movie__genres li{
    margin-right: 10px;
    font-size: 14px;
}

.movie .movie__summary{
    color: grey;
}